<template>
    <div>
        <div class="main_box">
            <el-table class="content_box"
                      height="700px"
                      :data="tableData">
                <el-table-column
                        class="el-table-column-contain"
                        prop="id"
                        label="id"
                        width="300">
                </el-table-column>
                <el-table-column
                        prop="oriName"
                        label=""
                        width="300">
                </el-table-column>
                <el-table-column
                        prop="createTime"
                        label=""
                        width="250">
                </el-table-column>
                <el-table-column
                        prop="countNum"
                        label=""
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="animalName"
                        label=""
                        width="300">
                </el-table-column>
                <el-table-column
                        prop="cycleLengthThreshold"
                        label="cycleLength"
                        width="130">
                </el-table-column>
                <el-table-column
                        prop="dustLengthThreshold"
                        label="dustLength"
                        width="130">
                </el-table-column>
                <el-table-column
                        prop="complete"
                        label=""
                        width="200">
                    <template slot-scope="scope">
                        <el-row>
                            <el-col :span="13">
                                <div>
                                    <el-button
                                            type="text"
                                            v-show="scope.row.complete === '2'"
                                            @click="toResult(scope)"
                                            disable-transitions>
                                    </el-button>
                                </div>
                            </el-col>
                            <el-col :span="5">
                                <div>
                                    <el-button type="danger" icon="el-icon-delete" circle
                                               v-show="scope.row.complete === '2'"
                                               @click="deleteResult(scope)"
                                               disable-transitions></el-button>
                                </div>
                            </el-col>
                        </el-row>
                        <div
                                style="height: 30px;"
                                v-show="scope.row.complete === '1'"
                                disable-transitions>
                        </div>
                        <el-row>
                            <el-col :span="13">
                                <div>
                                    <el-button
                                            type="text"
                                            v-show="scope.row.complete === '0'"
                                            @click="reSubmit(scope)"
                                            disable-transitions>
                                    </el-button>

                                </div>
                            </el-col>
                            <el-col :span="5">
                                <div>
                                    <el-button type="danger" icon="el-icon-delete" circle
                                               v-show="scope.row.complete === '0'"
                                               @click="deleteResult(scope)"
                                               disable-transitions></el-button>

                                </div>
                            </el-col>
                        </el-row>

                    </template>

                </el-table-column>
            </el-table>
            <el-button type="primary" class="addToList" @click="addFile"></el-button>
        </div>

    </div>
</template>

<script>

    import axios from 'axios';

    export default {
        mounted: function () {
            this.$bus.on("Config_forms", () => {
                this.check()
            })  //
            this.check()
        },
        methods: {

            reSubmit(scope) {
                this.$router.push({
                    name: 'submitView', params: {
                        data: scope.row
                    }
                })
            },

            check() {
                axios.get("/checkUser").then(response => {
                    this.tableData = response.data.data
                    for (let item of this.tableData) {
                        item.createTime = item.createTime.substr(0, 10)
                        item.oriName = item.oriName.split('.')[0]
                    }
                })
            },
            deleteResult(scope) {
                axios.get("/delete", {
                    params: {
                        id: scope.row.id,
                    }
                }).then(() => {
                    setTimeout("", 1000)
                    this.check()
                })


            },
            addFile() {
                this.$router.push({
                    name: 'submitView'
                })
            },
            toResult(scope) {
                this.$router.push({
                    name: 'result', params: {
                        data: scope.row
                    }
                })
            }
        },
        name: "main",
        data() {
            return {
                tableData: [{
                    id: '',
                    oriName: '',
                    complete: 1,
                    createTime: '',
                    countNum: '',
                    animalName: '',
                    cycleLengthThreshold: '',
                    dustLengthThreshold: '',
                }]
            }
        }
    }
</script>

<style scoped>
    .el-table-column-contain{
        background: #ffffffcc;
    }
    .addToList {

        z-index: 1;
        position: absolute;
    }

    .content_box {
        background: #ffffffcc;
        border-radius: 15px;
        /*box-shadow: 4px 4px #aaa;*/
        margin-bottom: 30px;
    }


    .main_box {
        display: inline-block;
        text-align: center;

    }


</style>